html {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  font-style: normal;
}

*, :after, :before {
  box-sizing: border-box;
}

:root {
  --color-blue-active: #00acdc;
}

ul {
  margin: 0;
  padding: 0;
}

body {
  box-sizing: inherit;
  background-color: #c9c8c8;
  margin: 0;
}

.container {
  max-width: 1200px;
}

.head {
  background: #fff;
  padding-left: 50px;
  padding-right: 50px;
  font-weight: 500;
}

.head__flex {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.head-nav-list {
  gap: 20px;
  list-style: none;
  display: flex;
}

a {
  text-transform: uppercase;
  color: #000;
  margin-bottom: auto;
  padding-top: 22.5px;
  padding-bottom: 22.5px;
  font-size: 12px;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.is-active {
  color: var(--color-blue-active);
  position: relative;
}

.is-active:after {
  content: "";
  background: var(--color-blue-active);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

a[data-qa="hover"]:hover {
  color: var(--color-blue-active);
}
